import styled from 'styled-components';

import React from 'react';
import { Transition } from 'react-spring';


import backgroundmp4 from "../images/360Bridge.mp4"

const BackgroundVideo = styled.video.attrs({
    preload: 'auto',
    autoPlay: true,
    loop: true,
    muted: true,
  })`
    opacity: 0.4;
    position: fixed;
    bottom: 5;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -100;
    left: 50%;
    transform: translateX(-50%);
  `;


const VideoBackground= () => {
  const isHome = true;
  return (
    <Transition items={isHome} from={{ opacity: 0 }} enter={{ opacity: 1 }} leave={{ opacity: 0 }}>
      {isHome => isHome
        && (styles => (
          <div style={styles} key="billboard">
            <BackgroundVideo>
              <source src={backgroundmp4} type="video/mp4" />
            </BackgroundVideo>
          </div>
        ))
      }
    </Transition>
  );
};

export default VideoBackground;
